import React, { useState } from "react";
import './PcAccount.css'
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function PcAccount() {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const myToken = sessionStorage.getItem('reborn-ec-token');
    const navigate = useNavigate();
    if (myToken) {
        axios.get('https://www.ict-reborn.link/api/user/detail', {
            headers: {
                'Authorization': `Bearer ${myToken}`
            }
        })
            .then(response => {
                console.log('Data sent successfully:', response.data.data);
                setEmail(response.data.data.email);
                console.log(email);
            })
            .catch(error => {
                console.error('Error sending data:', error);
            }, []);
    } else {
        console.error('No data found in sessionStorage');
    }

    const handleLogin = () => {
        let token;
        axios.post('https://www.ict-reborn.link/api/login', {
            email: email,
            password: password
        })
            .then((response) => {
                if (response.data.code === "202") {
                    token = response.data.data.token;
                    sessionStorage.setItem("reborn-ec-token", token);
                    sessionStorage.setItem("reborn-ec-email", email);
                    // 到期时间为1个月后
                    const expiryDate = new Date();
                    expiryDate.setMonth(expiryDate.getMonth() + 1);
                    sessionStorage.setItem("reborn-ec-token_expiry", expiryDate.getTime());
                    console.log(response.data.message);
                    navigate('/');
                    // 将URL更改为/desired-path
                } else {
                    alert(response.data.message);
                    console.log("Login unsuccessful:", response.data.message);
                }
            }).then(async () => {
                await axios.get('https://www.ict-reborn.link/api/user/detail', {
                    headers: {
                        'Authorization': `Bearer ${token}`,
                    }
                }).then((e) => {
                    console.log("-----user-----ID------");
                    console.log(e.data.data.id);
                    sessionStorage.setItem("reborn-ec-token-userID", e.data.data.id);
                }).then(() => {
                    navigate('/');
                })
            })
    }

    // 新用户注册功能
    const handleRegister = () => {
        navigate('/PcNewAccount');
    }

    return (
        <>
            <div className="PcAccount-container">
                <div className="Login-container">
                    <div className="Login-items-container">
                        <div className="Login-text">
                            ログイン
                        </div>
                        <input
                            type="text"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            placeholder="Email"
                        />
                        <input
                            type="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder="Password"
                        />
                        <button onClick={handleLogin}>ログイン</button>
                        <button onClick={handleRegister}>新規登録</button>
                        <button>パスワードリセット</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default PcAccount;
