import React, { useState } from "react";
import './PcMyPage.css'
import axios from "axios";


function PcMyPage() {
    const [MyPageName, setMyPageName] = useState('');
    const myToken = sessionStorage.getItem('reborn-ec-token');  //
    if (myToken) {
        axios.get('https://www.ict-reborn.link/api/user/detail', {
            headers: {
                'Authorization': `Bearer ${myToken}`
            }
        })
            .then(response => {
                console.log('Data sent successfully:', response.data.data);
                setMyPageName(response.data.data.username);
                console.log(MyPageName);
            })
            .catch(error => {
                console.error('Error sending data:', error);
            }, []);
    } else {
        console.error('No data found in sessionStorage');
    }

    return (
        <>
            <div className="MyPageNav">
                <div className="MyPageText">HOME / MY PAGE</div>
            </div>
            <div className="TEXTMyPage">
                MY PAGE
            </div>
            <div style={{
                backgroundColor: "white",
                width: "100%",
                height: "50px",
                display: "flex",
                justifyContent: "center",
            }}>
                <div style={{
                    display: "flex",
                    width: "544px",
                    borderTop: "1px solid black",
                    borderBottom: "1px solid black",
                    justifyContent: "center",
                    alignItems: "center",
                }}>
                    こんにちわ　　　<span className={"name"}>{MyPageName}</span>　様
                </div>
            </div>
            <div style={{
                backgroundColor: "white",
                width: "100%",
                height: "78px",
            }}></div>
            <div className="menu-container">
                <div className="menu-container1">
                    <div className="menu-container2">
                        <div className="NavTop"><a href="/PCFavorites">お気に入り</a></div>
                        <div className="NavTop"><a href="/PcShopHistory">購入履歴</a></div>
                        <div className="NavTop"><a href="/receipt">領収書</a></div>

                        <div className="NavTop"><a href="/PcAccountManagement">アカウント管理</a></div>
                        <div className="NavTop"><a href="/delivery-address">お届け先</a></div>
                    </div>
                </div>

            </div>

            <div style={{ height: '200px', width: '100%', backgroundColor: "#fff" }}></div> {/*占位*/}
        </>
    )
}

export default PcMyPage;
