import React, { useState } from "react";
import axios from "axios";
import './PcNewAccount.css'


function PcNewAccount() {


    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');
    const [tel, setTel] = useState('');

    const register = () => {

        axios.post('https://www.ict-reborn.link/api/admin/account/create', {
            email: email,
            password: password,
            phone: tel,
            roleId: 'CUSTOMER',
            status: 0,
            username: username,
        }).then(e => { console.log(e) }
        ).catch((error) => {
            console.error("Error logging in:", error);
        });

    }

    return (
        <>
            <div className="PcNewAccount-container">
                <div className="PcNew-container">
                    <div className="PcNew-items-container">
                        <div className="Login-text">
                            新規登録
                        </div>
                        <input
                            type="text"
                            onChange={(e) => setUsername(e.target.value)}
                            placeholder="Username"
                        />
                        <input
                            type="password"
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder="Password"
                        />
                        <input
                            type="email"
                            onChange={(e) => setEmail(e.target.value)}
                            placeholder="hello@japanmail.com"
                        />
                        <input
                            type="tel"
                            onChange={(e) => setTel(e.target.value)}
                            placeholder="08088888888"
                        />
                        <button onClick={register}>登録</button>

                    </div>
                </div>
            </div>
        </>
    )
}

export default PcNewAccount;