import React, { useEffect, useState } from "react";
import axios from "axios";
import './ProductDetailComponent.css';
import filterMoney from "../../../utils/filterMoney";
//const userId = sessionStorage.getItem('reborn-ec-token-userID');
function addCart(productId, onCartOperation) {
    const myToken = sessionStorage.getItem('reborn-ec-token');

    axios.get(`https://www.ict-reborn.link/api/sc/add/${productId}/1`, {
        headers: {
            'Authorization': `Bearer ${myToken}`
        }
    }).then(e => {
        onCartOperation();
        console.log(e);
    })
}

function deleteCart(productId, onCartOperation) {
    const myToken = sessionStorage.getItem('reborn-ec-token');

    axios.get(`https://www.ict-reborn.link/api/sc/delete/${productId}/1`, {
        headers: {
            'Authorization': `Bearer ${myToken}`
        }
    }).then(e => {
        console.log(e);
        onCartOperation();
    })
}



const ProductDetailComponent = ({ productId, quantity: initialQuantity, onCartOperation }) => {
    const [productDetail, setProductDetail] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [quantity, setQuantity] = useState(initialQuantity || 1);
    const url = `https://www.ict-reborn.link/api/product/detail/`;
    useEffect(() => {
        const url = `https://www.ict-reborn.link/api/product/detail/${productId}`;
        axios.get(url)
            .then(response => {
                setProductDetail(response.data.data.product);
                setLoading(false);

            })
            .catch(err => {
                setError(err.message);
                setLoading(false);
            });
    }, [productId]);

    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error: {error}</p>;

    function deleteAllCart(productId, onCartOperation) {
        const myToken = sessionStorage.getItem('reborn-ec-token');

        axios.get(`https://www.ict-reborn.link/api/sc/delete/${productId}/${quantity}`, {
            headers: {
                'Authorization': `Bearer ${myToken}`
            }
        }).then(e => {
            onCartOperation();
            console.log(e);
        })
    }

    return (
        <div className="ProductDetailComponent-container">
            <div className="ProductDetailComponent-container-left">
                <img src={productDetail.image1} alt={productDetail.name} />
            </div>
            <div className="ProductDetailComponent-container-right">
                <div className="productName item">{productDetail.name}</div>
                <p className="price">{filterMoney(productDetail.price)}</p>
                <p className="otherItem">在庫数: {productDetail.inventoryQuantity}</p>
                <p className="otherItem number">品番: {productDetail.productId}</p>
                <div style={{
                    display: "flex",
                    alignItems: "center",
                    marginLeft: "-11px",
                }}>
                    <button onClick={() => {
                        setQuantity(prevQuantity => {
                            const newQuantity = Math.max(prevQuantity - 1, 0);
                            if (newQuantity > 0) {
                                deleteCart(productDetail.id, onCartOperation);
                            }
                            return newQuantity;
                        });
                    }}
                    >
                        <svg t="1695278246493" className="icon" viewBox="0 0 1024 1024" version="1.1"
                            xmlns="http://www.w3.org/2000/svg" p-id="3208" width="200" height="200">
                            <path
                                d="M512 938.666667C276.362667 938.666667 85.333333 747.637333 85.333333 512S276.362667 85.333333 512 85.333333s426.666667 191.029333 426.666667 426.666667-191.029333 426.666667-426.666667 426.666667z m0-64c200.298667 0 362.666667-162.368 362.666667-362.666667S712.298667 149.333333 512 149.333333 149.333333 311.701333 149.333333 512s162.368 362.666667 362.666667 362.666667zM352 480h320a32 32 0 0 1 0 64H352a32 32 0 0 1 0-64z"
                                fill="#000000" p-id="3209"></path>
                        </svg>
                    </button>
                    <div className="quantity" style={{ fontSize: "22px" }}> {quantity} </div>
                    <button onClick={() => {
                        setQuantity(q => Math.min(q + 1, productDetail.inventoryQuantity));
                        addCart(productDetail.id, onCartOperation);

                    }
                    }>
                        <svg t="1695278225591" className="icon" viewBox="0 0 1024 1024" version="1.1"
                            xmlns="http://www.w3.org/2000/svg" p-id="3069" width="200" height="200">
                            <path
                                d="M512 938.666667C276.362667 938.666667 85.333333 747.637333 85.333333 512S276.362667 85.333333 512 85.333333s426.666667 191.029333 426.666667 426.666667-191.029333 426.666667-426.666667 426.666667z m0-64c200.298667 0 362.666667-162.368 362.666667-362.666667S712.298667 149.333333 512 149.333333 149.333333 311.701333 149.333333 512s162.368 362.666667 362.666667 362.666667z m32-394.666667h128a32 32 0 0 1 0 64H544v128a32 32 0 0 1-64 0V544H352a32 32 0 0 1 0-64h128V352a32 32 0 0 1 64 0v128z"
                                fill="#000000" p-id="3070"></path>
                        </svg>
                    </button>
                    <button onClick={
                        () => {
                            deleteAllCart(productDetail.id, onCartOperation);
                        }
                    }>

                        <svg t="1695278267498" className="icon" viewBox="0 0 1024 1024" version="1.1"
                            xmlns="http://www.w3.org/2000/svg" p-id="3347" width="200" height="200">
                            <path
                                d="M202.666667 256h-42.666667a32 32 0 0 1 0-64h704a32 32 0 0 1 0 64H266.666667v565.333333a53.333333 53.333333 0 0 0 53.333333 53.333334h384a53.333333 53.333333 0 0 0 53.333333-53.333334V352a32 32 0 0 1 64 0v469.333333c0 64.8-52.533333 117.333333-117.333333 117.333334H320c-64.8 0-117.333333-52.533333-117.333333-117.333334V256z m224-106.666667a32 32 0 0 1 0-64h170.666666a32 32 0 0 1 0 64H426.666667z m-32 288a32 32 0 0 1 64 0v256a32 32 0 0 1-64 0V437.333333z m170.666666 0a32 32 0 0 1 64 0v256a32 32 0 0 1-64 0V437.333333z"
                                fill="#000000" p-id="3348"></path>
                        </svg>
                    </button>
                </div>
            </div>
        </div>
    );
};

export default ProductDetailComponent;


